<template>
  <main class="row main">
    <!-- need to set min height -->
    <div class="settings-panel">
      <normal-settings @change="onNormalSettingsChange"></normal-settings>
      <board-settings @change="onBoardSettingsChange"></board-settings>
      <div class="row operations">
        <el-button type="primary" size="small" round @click="onRunBtnClick">运行模型</el-button>
      </div>
    </div>
    <div class="result-panel">
      <div class="row header">
        <span class="result-text">运行结果</span>
        <el-button type="primary" size="small" round @click="onDiffBtnClick" class="diff-btn" :disabled="!heatMap">
          生成对比结果
        </el-button>
      </div>
      <div class="chart-panel" v-loading="isLoading" :element-loading-text="modelLoadingText"
           element-loading-background="transparent">
        <span class="title">模型预测结果热力图</span>
        <bar-chart v-if="heatMap" :data="heatMap.model"></bar-chart>
      </div>
      <div class="chart-panel" v-loading="isLoading" :element-loading-text="femLoadingText"
           element-loading-background="transparent">
        <span class="title">有限元分析结果热力图</span>
        <bar-chart v-if="heatMap" :data="heatMap.fem"></bar-chart>
      </div>
    </div>
    <el-dialog :visible.sync="dialogVisible" :close-on-click-modal="false" :modal="false">
      <div class="chart-panel" v-if="dialogVisible">
        <span class="title">对比结果热力图</span>
        <bar-chart v-if="heatMap" :data="heatMap.diff"></bar-chart>
      </div>
    </el-dialog>
  </main>
</template>

<script>
import BarChart from '../components/BarChart';
import BoardSettings from '../components/BoardSettings';
import NormalSettings from '../components/NormalSettings';

import modelRest from '../rest/modelRest';

export default {
  data() {
    return {
      heatMap: null,
      settings: null,
      grid: null,
      dialogVisible: false,
      isLoading: false,
      modelLoadingText: 'Inference',
      femLoadingText: '数据预处理'
    };
  },
  methods: {
    onRunBtnClick() {
      this.heatMap = null;
      this.dialogVisible = false;
      this.isLoading = true;
      // start loading indicator
      this.femLoadingText = '数据预处理中';
      const indicatorPromise = this.setupLoadingText('数据预处理中')
        .then(() => this.setupLoadingText('方程构建中'))
        .then(() => this.setupLoadingText('方程模拟中'))
        .then(() => this.setupLoadingText('结果显示中'));
      const restPromise = modelRest.getModelResult(Object.assign({}, this.settings, { grid: this.grid }));
      Promise.all([indicatorPromise, restPromise]).then(dataList => {
        this.setupLoadingText('结果显示中').then(() => {
          this.isLoading = false;
          this.heatMap = dataList[1];
        });
      });
    },
    setupLoadingText(text, interval = 500) {
      return new Promise(resolve => {
        setTimeout(() => {
          this.femLoadingText = text;
          resolve();
        }, interval);
      });
    },
    onBoardSettingsChange(data) {
      this.grid = data;
    },
    onNormalSettingsChange(data) {
      this.settings = data;
    },
    onDiffBtnClick() {
      this.dialogVisible = true;
    }
  },
  components: {
    BarChart,
    BoardSettings,
    NormalSettings
  },
  mounted() {
  }
};
</script>
<style lang="scss" scoped>
  .main {
    .settings-panel {
      width: 600px; // could change
      background-color: #18272f;

      .operations {
        justify-content: flex-end;
        align-items: center;
        height: 88px;
        padding: 0 32px;
      }
    }

    .result-panel {
      width: calc(100% - 600px);
      padding: 0 32px;
      background-color: #0f1d24;

      .header {
        justify-content: space-between;
        margin-top: 16px;

        .result-text {
          color: white;
        }
        .diff-btn {
          background-color: #2e3a43;
          color: #ffffff80;
          border: none;
          &:hover {
            background-color: #2e3a43;
            color: #ffffff;
          }
        }
      }
    }

    .chart-panel {
      position: relative;
      height: 400px;
      margin-top: 16px;
      background-color: #080e14;

      & > .title {
        position: absolute;
        top: 24px;
        left: 24px;
        color: white;
        z-index: 10;
      }
    }

    /deep/ {
      .el-dialog__wrapper {
        overflow: hidden;
        right: 24px;
        left: auto;
      }

      .el-dialog {
        width: calc(100vw - 648px);
        background-color: #18272f;
      }
    }
  }
</style>
